@use "./sass/helpers/variables" as *;

header {
    height: 4.5rem;
    box-shadow: $main-box-shadow;
    @media (max-width: 767px) {
        .row > div {
            height: 37px;
        }
    }
    img {
        width: 130px;
    }
    .logo {
        a {
            color: $blue-color;
        }
    }
    .nav-bar {
        @media (max-width: 767px) {
            position: fixed;
            z-index: 1000;
            background-color: #fff;
            right: -100%;
            top: 0;
            width: 100%;
            height: 100%;
            padding: 0;
            transition: $main-transition;
            &.show {
                right: 0;
            }
            .head {
                border-bottom: 1px solid $alt-grey-color;
                margin-bottom: 1rem;
                height: 4.5rem;
                a {
                    color: $blue-color;
                }
                .close-btn:hover button {
                    color: $blue-color;
                }
            }
            li {
                width: 80%;
                margin: auto;
                border-bottom: 1px solid $alt-grey-color;
                a {
                    display: inline-block;
                    width: 100%;
                    height: 100%;
                    padding: 1rem;
                    transition: $main-transition;
                    &:hover {
                        background-color: $alt-grey-color;
                    }
                }
            }
        }
        a {
            color: #000;
            transition: $main-transition;
            &.active,
            &:hover {
                color: $blue-color;
            }
        }
    }
    .header-search {
        display: inline-block;
        width: 200px;
        height: 35px;
        position: relative;
        @media (max-width: 767px) {
            width: calc(100% - 60px);
        }
        form {
            width: 100%;
            height: 100%;
            position: relative;
            .header-search-inp {
                background-color: $alt-grey-color;
                width: 100%;
                height: 100%;
                padding: 0 3rem 0 1rem;
                border: none;
                outline: none;
                border-radius: 1.25rem; // 20px
                transition: $main-transition;
                @media (max-width: 767px) {
                    width: 30px;
                    padding: 0;
                    &.active {
                        width: 100%;
                        padding: 0 3rem 0 1rem;
                    }
                }
                &::placeholder {
                    transition: $main-transition;
                }
                &:focus::placeholder {
                    opacity: 0;
                }
            }
            button {
                position: absolute;
                top: 0;
                right: 0;
                height: 100%;
                border: 0;
                outline: none;
                background-color: $blue-color;
                color: #fff;
                width: 40px;
                border-top-right-radius: 1.25rem; // 20px
                border-bottom-right-radius: 1.25rem; // 20px
                display: flex;
                justify-content: center;
                align-items: center;
                @media (max-width: 767px) {
                    border-radius: 50%;
                    width: 35px;
                    font-size: 14px;
                    &.active {
                        width: 40px;
                        border-top-right-radius: 1.25rem; // 20px
                        border-bottom-right-radius: 1.25rem; // 20px
                        border-top-left-radius: 0; // 20px
                        border-bottom-left-radius: 0; // 20px
                    }
                }
            }
        }
        .search-error {
            text-align: left;
            background-color: #F44336;
            color: #fff;
            position: absolute;
            left: 0;
            top: 135%;
            padding: 0.5rem 1rem;
            opacity: 0;
            animation: show-error $main-transition forwards;
            &::before {
                content: "";
                position: absolute;
                top: -20px;
                left: 50%;
                transform: translateX(-50%);
                border-width: 10px;
                border-style: solid;
                border-color: transparent transparent #F44336 transparent;
            }
        }
    }
    .toggle-btn:hover {
        color: $blue-color;
        transition: $main-transition
    }
}

@keyframes show-error {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}