@use "./sass/helpers/variables" as *;

// Start Landing
.landing {
    h1 {
        width: fit-content;
    }
    img {
        display: block;
        width: 100%;
        margin: 0 auto;
        @media (min-width: 768px) {
            width: 75%;
        }
    }
    span {
        display: block;
        text-align: center;
        margin-top: 1rem;
    }
}
// End Landing
//- Start Post Content
.post {
    .page-table {
        padding: 1rem;
        background-color: $blue-color;
        border-radius: $main-border-radius;
        @media (max-width: 991px) {
            &.pin-table {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
            }
        }
        @media (min-width: 992px) {
            position: sticky;
            top: 1rem;
        }
        .table-head {
            border-bottom: 1px solid #fff;
            padding-bottom: 0.5rem;
            .table-title {
                font-size: 20px;
                color: #fff;
            }
            .show-btn {
                color: #fff;
                cursor: pointer;
            }
        }
        .table-links {
            transition: 0.3s;
            @media (max-width: 991px) {
                &.hide {
                    height: 0;
                    overflow: hidden;
                    padding-top: 0 !important;
                }
            }
            .title-link {
                color: #fff;
                display: block;
                cursor: pointer;
                &:not(:last-of-type) {
                    margin-bottom: 0.325rem;
                }
            }
        }
    }
    .post-content {
        .post-paragraph {
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            .paragraph-title {
                font-size: 1.5rem;
                font-weight: bold;
                color: $blue-color;
            }
            .paragraph-description {
                line-height: $main-line-height;
                margin: 0;
            }
        }
    }
}
//- End Post Content
// Start Related Posts
.related-posts {
    .row > div {
        padding-bottom: 0.75rem;
    }
}
// End Related Posts