@use "../helpers/variables" as *;

footer {
    background-color: #333;
    .navigation,
    .company {
        h5 {
            font-size: 1rem;
        }
        a {
            font-size: 0.875rem; // 14px
            transition: $main-transition;
            &:hover {
                color: #fff !important;
            }
        }
    }
    .newsletter {
        form {
            max-width: 450px;
            input[type="email"] {
                width: 290px;
                padding: 0.5rem;
                border: none;
                outline: none;
                flex: 1;
                &::placeholder {
                    transition: $main-transition;
                }
                &:focus::placeholder {
                    opacity: 0;
                }
            }
            input[type="submit"] {
                width: 135px;
                padding: 0.5rem;
                border: none;
                outline: none;
                background-color: $blue-color;
                color: #fff;
            }
        }
    }
    .copyright {
        border-top: 1px solid $alt-grey-color;
        span {
            color: $blue-color;
        }
    }
}