@use "../helpers/variables" as *;

.main-btn {
    width: fit-content;
    background-color: $blue-color;
    color: #fff;
    padding: 0.5rem 1.1rem;
    transition: $main-transition;
    border: none;
    outline: none;
}

.scroll-to-top {
    background-color: #2196F3;
    color: #fff;
    position: fixed;
    right: -100%;
    bottom: 15px;
    z-index: 999;
    border-radius: $main-border-radius;
    transition: $main-transition;
    &:hover,
    &:active {
        background-color: $blue-color !important;
        color: #fff !important;
    }
}