@use "./sass/helpers/variables" as *;

.search-popup {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1005;
    background-color: rgba(#000, 0.5);
    width: 100%;
    height: 100%;
    padding: 1rem;
    transform: scale(0);
    animation: show-results $main-transition forwards;
    @media (min-width: 768px) {
        padding: 2rem;
    }
    &.close {
        transition: $main-transition;
        scale: 0;
    }
    .search-container {
        background: #fff;
        width: 100%;
        height: 100%;
        padding: 1rem;
        border-radius: $main-border-radius;
        overflow: auto;
        &::-webkit-scrollbar {
            width: 10px;
        }
        &::-webkit-scrollbar-track { 
            background-color: transparent;
        }
        &::-webkit-scrollbar-thumb {
            background-color: $alt-grey-color;
            border-radius: $main-border-radius;
        }
        &::-webkit-scrollbar-thumb:hover {
            background-color: $grey-color;
        }
        .search-head {
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid $grey-color;
            h2 {
                .search-word {
                    color: $blue-color;
                    font-weight: bold;
                }
            }
            .search-close-btn {
                transition: $main-transition;
                &:hover {
                    color: $blue-color;
                }
            }
        }
        .search-result {
            .result-box {
                border-bottom: 1px solid $alt-grey-color;
                transition: $main-transition;
                &:hover {
                    background-color: $alt-grey-color;
                }
            }
            .default-result {
                p {
                    span {
                        color: $blue-color;
                    }
                }
                .search-again {
                    border-radius: $main-border-radius;
                }
            }
        }
    }
}
@keyframes show-results {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}

.breadcrumb-holder {
    background-color: $alt-grey-color;
    li:not(:last-of-type) {
        a {
            color: $blue-color !important;
            &:hover {
                color: $blue-color !important;
            }
        }
    }
}

.post-box {
    border-radius: $main-border-radius;
    max-width: 390px;
    transition: $main-transition;
    transform: translateY(20px);
    animation: show-box $main-transition linear forwards;
    &:hover {
        background-color: $alt-grey-color;
    }
    img {
        width: 100%;
        height: 175px;
        border-radius: $main-border-radius;
    }
}
.no-posts-default {
    transform: translateY(20px);
    animation: show-box $main-transition linear forwards;
}

@keyframes show-box {
    0% {
        transform: translateY(20px);
    }
    100% {
        transform: translateY(0);
    }
}

// Error Post
.error-post {
    padding: 1rem;
    width: fit-content;
    .error-info {
        margin-bottom: 1rem;
        .error-icon {
            display: inline-block;
            margin-bottom: 0.5rem;
            color: $blue-color;
        }
        h4 {
            margin: 0;
        }
    }
    .reload-page {
        p {
            margin-bottom: 0.5rem;
        }
        button {
            border-radius: $main-border-radius;
            border: 1px solid transparent;
            transition: $main-transition;
            &:hover {
                background-color: #fff;
                color: $blue-color;
                border-color: $blue-color;
            }
        }
    }
}


