* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: sans-serif;
}

ul {
    list-style: none;
    margin: 0 !important;
}

a {
    text-decoration: none;
}

.btn {
    border-color: transparent !important;
}